
.Search{
    margin-bottom: 5vmin;
    z-index: 10;
}

.Search-input-wrapper{
    position: relative;
    width:60vmin;
}
.Search-input-wrapper input{
    width:100%;
    height:5vmin;
    outline:none;
    letter-spacing: 0.3vmin;
    border:none;
    border-bottom: 1px solid black;
    background-color: rgb(249, 252, 252);;
}
.Search-image{
    position: absolute;
    width:4vmin;
    right:0.5vmin;
    top:0.5vmin;
}
